import React from "react";
import Placeholder from "./epcot.png";
import "./Results.css";
import MK from "./mk2.jpg";
import AK from "./ak2.jpg";
import HS from "./hs2.jpg";
import EP from "./ep2.jpg";
import NaP from "./misc 2.jpg";
import $ from "./money1.png";
import $$ from "./money2.png";
import $$$ from "./money3.png";
import $$$$ from "./money4.png";
import BLDS from "./BLDS.png";
import B from "./B.png";
import BD from "./BD.png";
import BDS from "./bds.png";
import BLD from "./BLD.png";
import D from "./D.png";
import L from "./L.png";
import LDS from "./LDS.png";
import S from "./BLDS.png";
import LD from "./LD.png";

function SingleResultBox(props) {
  function getHeaderinator(park) {
    if (park === "MK") {
      return MK;
    } else if (park === "AK") {
      return AK;
    } else if (park === "HS") {
      return HS;
    } else if (park === "EP") {
      return EP;
    } else if (park === "NaP") {
      return NaP;
    } else {
      return Placeholder;
    }
  }

  function getPriceinator(mon) {
    if (mon === "$") {
      return $;
    } else if (mon === "$$") {
      return $$;
    } else if (mon === "$$$") {
      return $$$;
    } else if (mon === "$$$$") {
      return $$$$;
    } else {
      return $;
    }
  }

  function getMealinator(meal) {
    if (meal === "B") {
      return B;
    } else if (meal === "BD") {
      return BD;
    } else if (meal === "BLD") {
      return BLD;
    } else if (meal === "BLDS") {
      return BLDS;
    } else if (meal === "S") {
      return S;
    } else if (meal === "LDS") {
      return LDS;
    } else if (meal === "BDS") {
      return BDS;
    } else if (meal === "D") {
      return D;
    } else if (meal === "L") {
      return L;
    } else {
      return LD;
    }
  }
  return (
    <div className="singleResultBox">
      <img
        className="restaurantImage"
        alt="placeholder"
        src={getHeaderinator(props.park)}
      />
      <div className="restaurantTitle">{props.restaurantName}</div>
      <div className="restaurantDescription">{props.description}</div>
      <div className="restaurantInfoBox">
        <img
          className="restaurantMoney"
          alt="price"
          src={getPriceinator(props.money)}
        />
        <img
          className="restaurantMoney"
          alt="meal"
          src={getMealinator(props.meals)}
        />
      </div>
    </div>
  );
}

export default SingleResultBox;
